import { useState } from "react";
import "./styles.css";
import { Card } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
export default function App() {
  const [d, sd] = useState([{ na: "sample", ag: 20 }]);
  const [indd, sindd] = useState(0);
  const [f, sf] = useState(false);
  const add = (v) => {
    sd([...d, v]);
  };
  const deletes = (ind) => {
    d.splice(ind, 1);
    sd(d);
  };
  const edit = (ind) => {
    sf(true);
    sindd(ind);
  };
  const update = (v, ind) => {
    console.log(d, ind, v, d[ind]);
    d[ind].na = v.na;
    d[ind].ag = v.ag;
    sd(d);
    sf(false);
  };
  return (
    <div className="App">
      {f ? <Edit ind={indd} d={d} update={update} /> : <Add add={add} />}
      {d &&
        d.map((i, ind) => (
          <Card>
            <Card.Body>
              <Display
                key={ind.toString()}
                ind={ind}
                d={i}
                deletes={deletes}
                edit={edit}
              />
            </Card.Body>
          </Card>
        ))}
    </div>
  );
}
const Display = ({ d, ind, deletes, edit }) => {
  return (
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Age</th>
        </tr>
      </thead>
      <tbody>
        {
          <tr>
            <td>{d.na}</td>
            <td>{d.ag}</td>
            <td>
              <button onClick={() => deletes(ind)}>Delete</button>
            </td>
            <td>
              <button onClick={() => edit(ind)}>Edit</button>
            </td>
          </tr>
        }
      </tbody>
    </table>
  );
};
const Add = ({ add }) => {
  const [na, sna] = useState("");
  const [ag, sag] = useState("");
  const hs = (e) => {
    e.preventDefault();
    if (typeof ag === "number") {
      add({ na, ag });
      sna("");
      sag("");
    } else {
      alert("enter numeric");
    }
  };
  return (
    <form onSubmit={(e) => hs(e)}>
      <div style={{ display: "flex" }}>
        <span styel={{ width: "30%" }}>Name:</span>
        <span styel={{ width: "70%" }}>
          {" "}
          <input type="text" value={na} onChange={(e) => sna(e.target.value)} />
        </span>
      </div>
      <div style={{ display: "flex" }}>
        <span styel={{ width: "30%" }}>Age:</span>
        <span styel={{ width: "70%" }}>
          <input
            type="text"
            value={ag}
            onChange={(e) => sag(+e.target.value)}
          />
        </span>
      </div>
      <button type="submit">Add</button>
    </form>
  );
};

const Edit = ({ ind, d, update }) => {
  const [na, sna] = useState(d[ind].na);
  const [ag, sag] = useState(d[ind].ag);
  const hs = (e) => {
    e.preventDefault();
    if (typeof ag === "number") {
      sna({ na });
      sag({ ag });
    } else {
      alert("enter numeric");
    }
  };
  return (
    <form onSubmit={(e) => hs(e)}>
      <div style={{ display: "flex" }}>
        <span styel={{ width: "30%" }}>Name:</span>
        <span styel={{ width: "70%" }}>
          {" "}
          <input type="text" value={na} onChange={(e) => sna(e.target.value)} />
        </span>
      </div>
      <div style={{ display: "flex" }}>
        <span styel={{ width: "30%" }}>Age:</span>
        <span styel={{ width: "70%" }}>
          <input
            type="text"
            value={ag}
            onChange={(e) => sag(+e.target.value)}
          />
        </span>
      </div>
      <button type="submit" onClick={() => update({ na, ag }, ind)}>
        update
      </button>
    </form>
  );
};
